a {
  color: #590000; }

body {
  margin: 0;
  background: #E25252; }

header {
  height: 10em;
  background: #E25252; }

header h1 {
  font-family: 'Fjalla One', sans-serif;
  color: #590000; }

nav {
  background: #CF2626;
  height: 10em;
  border: solid 1px; }

.text-center {
  text-align: center;
  margin-top: 1em;
  font-size: 6em; }

.btn {
  position: absolute;
  bottom: 0em;
  right: -15em;
  width: 10em;
  padding-top: 10px;
  padding-bottom: 45px;
  background: #590000;
  border: solid 1px black;
  border-radius: 0px;
  transition: all 0.5s;
  font-size: 1.3em;
  color: white; }

@media screen and (max-width: 992px) {
  .btn {
    position: relative;
    left: 50%;
    top: 2em;
    transform: translateX(-50%); }
  nav {
    height: 16em; }
  header {
    height: 15em; } }

.btn:hover {
  background: #CF2626;
  color: black; }
